<template>
  <section
    class="over-hidden position-relative flex-column just-center align-center hy-back"
  >
    <img
      src="../../../assets/svg/dashboard/data-charts/hydrological-texture/triangle.svg"
      alt="三角形图"
      class="img-suite"
      :style="{ height: height * 1.2 + 'px' }"
    />
    <ul
      class="mb0 flex-column just-center align-center position-absolute hy-left-content"
    >
      <li class="hy-text">
        {{ hydrologicalTextureData[curIndex].name }}
      </li>
      <li
        class="hy-number"
        :class="{
          'one-level-color': hydrologicalTextureData[curIndex].value >= 20,
          'two-level-color':
            hydrologicalTextureData[curIndex].value >= 10 &&
            hydrologicalTextureData[curIndex].value < 20
        }"
      >
        {{ hydrologicalTextureData[curIndex].value }}
      </li>
    </ul>
    <ul
      class="mb0 flex-column just-center align-center position-absolute hy-center-content"
    >
      <li class="hy-text">
        {{ hydrologicalTextureData[curIndex + 1].name }}
      </li>
      <li
        class="hy-number"
        :class="{
          'one-level-color': hydrologicalTextureData[curIndex + 1].value >= 20,
          'two-level-color':
            hydrologicalTextureData[curIndex + 1].value >= 10 &&
            hydrologicalTextureData[curIndex + 1].value < 20
        }"
      >
        {{ hydrologicalTextureData[curIndex + 1].value }}
      </li>
    </ul>
    <ul
      class="mb0 flex-column just-center align-center position-absolute hy-right-content"
    >
      <li class="hy-text">
        {{ hydrologicalTextureData[curIndex + 2].name }}
      </li>
      <li
        class="hy-number"
        :class="{
          'one-level-color': hydrologicalTextureData[curIndex + 2].value >= 20,
          'two-level-color':
            hydrologicalTextureData[curIndex + 2].value >= 10 &&
            hydrologicalTextureData[curIndex + 2].value < 20
        }"
      >
        {{ hydrologicalTextureData[curIndex + 2].value }}
      </li>
    </ul>
  </section>
</template>

<script lang="ts">
import {
  computed,
  defineComponent,
  inject,
  onMounted,
  PropType,
  ref,
  Ref
} from "vue";
import { useInterval } from "@/hooks/common/useTimer";

export default defineComponent({
  name: "hydrologicalTexture",
  components: {},
  props: {
    hydrologicalTextureData: {
      type: Array as PropType<{ name: string; value: number }[]>,
      default: () => [
        { name: "水位", value: 0 },
        { name: "水压", value: 0 },
        { name: "流量", value: 0 },
        { name: "流速", value: 0 },
        { name: "排水量", value: 0 },
        { name: "水质", value: 0 },
        { name: "地表", value: 0 },
        { name: "降水量", value: 0 },
        { name: "水位", value: 0 }
      ]
    },
    wait: {
      type: Number,
      default: 3000
    }
  },
  emits: {},
  setup(props) {
    const curIndex = ref<number>(0);
    const length = computed(() =>
      Math.ceil(props.hydrologicalTextureData.length / 2)
    );

    function setCurIndex() {
      curIndex.value = (curIndex.value + 1) % length.value;
    }

    const { openInterval } = useInterval({ cb: setCurIndex, dur: props.wait });

    onMounted(() => {
      openInterval();
    });
    return {
      curIndex,
      height: inject<Ref<number>>("height")
    };
  }
});
</script>

