<template>
  <div
    class="w100 h100 over-hidden position-relative flex-column just-end align-center"
  >
    <img
      src="../../../assets/imgs/dashboard/data-charts/side-slope-back.png"
      alt="边坡图"
      class="img-suite"
      :style="{ height: height * 0.8 + 'px' }"
    />
    <ul
      class="mb0 flex-column just-center align-center position-absolute sl-left-content"
    >
      <li
        class="sl-number"
        :class="{
          'one-level-color': sideSlopeData[curIndex].value >= 20,
          'two-level-color':
            sideSlopeData[curIndex].value >= 10 &&
            sideSlopeData[curIndex].value < 20
        }"
      >
        {{ sideSlopeData[curIndex].value }}
      </li>
      <li class="sl-text">{{ sideSlopeData[curIndex].name }}</li>
    </ul>
    <ul
      class="mb0 flex-column just-center align-center position-absolute sl-center-content"
    >
      <li
        class="sl-number"
        :class="{
          'one-level-color': sideSlopeData[curIndex + 1].value >= 20,
          'two-level-color':
            sideSlopeData[curIndex + 1].value >= 10 &&
            sideSlopeData[curIndex + 1].value < 20
        }"
      >
        {{ sideSlopeData[curIndex + 1].value }}
      </li>
      <li class="sl-text">{{ sideSlopeData[curIndex + 1].name }}</li>
    </ul>
    <ul
      class="mb0 flex-column just-center align-center position-absolute sl-right-content"
    >
      <li
        class="sl-number"
        :class="{
          'one-level-color': sideSlopeData[curIndex + 2].value >= 20,
          'two-level-color':
            sideSlopeData[curIndex + 2].value >= 10 &&
            sideSlopeData[curIndex + 2].value < 20
        }"
      >
        {{ sideSlopeData[curIndex + 2].value }}
      </li>
      <li class="sl-text">{{ sideSlopeData[curIndex + 2].name }}</li>
    </ul>
  </div>
</template>

<script lang="ts">
import {
  computed,
  defineComponent,
  inject,
  onMounted,
  PropType,
  ref,
  Ref
} from "vue";
import { useInterval } from "@/hooks/common/useTimer";

interface IView {
  curIndex: number;
  timer: number;
}

export default defineComponent({
  name: "slopeMonitoring",
  props: {
    sideSlopeData: {
      type: Array as PropType<{ name: string; value: number }[]>,
      default: () => [
        { name: "雨量异常", value: 0 },
        { name: "倾斜异常", value: 0 },
        { name: "表面位移", value: 0 },
        { name: "一级报警", value: 0 },
        { name: "二级报警", value: 0 },
        { name: "三级报警", value: 0 },
        { name: "四级报警", value: 0 },
        { name: "掉线", value: 0 },
        { name: "雨量异常", value: 0 }
      ]
    },
    wait: {
      type: Number,
      default: 3000
    }
  },
  setup(props) {
    const curIndex = ref<number>(0);
    const length = computed(() => Math.ceil(props.sideSlopeData.length / 2));

    function setCurIndex() {
      curIndex.value = (curIndex.value + 1) % length.value;
    }

    const { openInterval } = useInterval({ cb: setCurIndex, dur: props.wait });

    onMounted(() => {
      openInterval();
    });

    return { curIndex, height: inject<Ref<number>>("height") };
  }
});
</script>

