
import {
  computed,
  defineComponent,
  inject,
  onMounted,
  PropType,
  ref,
  Ref
} from "vue";
import { useInterval } from "@/hooks/common/useTimer";

interface IView {
  curIndex: number;
  timer: number;
}

export default defineComponent({
  name: "slopeMonitoring",
  props: {
    sideSlopeData: {
      type: Array as PropType<{ name: string; value: number }[]>,
      default: () => [
        { name: "雨量异常", value: 0 },
        { name: "倾斜异常", value: 0 },
        { name: "表面位移", value: 0 },
        { name: "一级报警", value: 0 },
        { name: "二级报警", value: 0 },
        { name: "三级报警", value: 0 },
        { name: "四级报警", value: 0 },
        { name: "掉线", value: 0 },
        { name: "雨量异常", value: 0 }
      ]
    },
    wait: {
      type: Number,
      default: 3000
    }
  },
  setup(props) {
    const curIndex = ref<number>(0);
    const length = computed(() => Math.ceil(props.sideSlopeData.length / 2));

    function setCurIndex() {
      curIndex.value = (curIndex.value + 1) % length.value;
    }

    const { openInterval } = useInterval({ cb: setCurIndex, dur: props.wait });

    onMounted(() => {
      openInterval();
    });

    return { curIndex, height: inject<Ref<number>>("height") };
  }
});
