<template>
  <div class="position-relative flex-column just-center over-hidden">
    <img
      src="~@/assets/svg/dashboard/data-charts/safety-monitoring/safety-back.svg"
      alt=""
      class="img-suite"
      :style="{ height: height + 'px' }"
    />
    <div class="flex-row align-center just-around safe-top">
      <span class="indicator color-FF25">
        <NumAnimate :round="1">{{ safetyData[curIndex].warning }}</NumAnimate>
      </span>
      <span class="color-FFD9">
        {{ safetyData[curIndex].name }}
      </span>
      <span class="indicator color-FFD1">
        <NumAnimate :round="1">
          {{ safetyData[curIndex].accurate }}
        </NumAnimate>
      </span>
    </div>
    <div class="flex-row just-around align-center safe-center">
      <span class="color-FF00">报警</span>
      <span class="img-wrapper">
        <img
          v-for="item in length"
          :key="item"
          class="scanningImg"
          src="~@/assets/svg/dashboard/data-charts/safety-monitoring/scanning.svg"
          alt="轮播图片"
          :style="{
            rotate: (360 / length) * (item - 1) + 'deg',
            opacity: item - 1 === curIndex ? 1 : '0.5'
          }"
          @click="goTo(item)"
        />
      </span>
      <span class="color-FFE5">调校</span>
    </div>
    <div class="flex-row align-center just-around safe-bottom">
      <span class="indicator color-FED2">
        <NumAnimate :round="1">
          {{ safetyData[curIndex + 1].warning }}
        </NumAnimate>
      </span>
      <span class="color-FFD9">
        {{ safetyData[curIndex + 1].name }}
      </span>
      <span class="indicator color-FFD9">
        <NumAnimate :round="1">
          {{ safetyData[curIndex + 1].accurate }}
        </NumAnimate>
      </span>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, inject, onMounted, ref, Ref } from "vue";
import NumAnimate from "@/components/dashboard/common/num-animate.vue";
import { useInterval } from "@/hooks/common/useTimer";
export default defineComponent({
  name: "dataChartsSafetyMonitor",
  components: { NumAnimate },
  props: {
    safetyData: {
      type: Array,
      default: () => []
    },
    wait: {
      type: Number,
      default: 3000
    }
  },

  setup(props) {
    const curIndex = ref<number>(0);
    const length = computed(() => Math.ceil(props.safetyData.length / 2));

    function setCurIndex() {
      curIndex.value = (curIndex.value + 1) % length.value;
    }

    const { openInterval } = useInterval({ cb: setCurIndex, dur: props.wait });

    onMounted(() => {
      openInterval();
    });

    return {
      curIndex,
      goTo(index: number) {
        curIndex.value = index + 1;
      },
      height: inject<Ref<number>>("height"),
      length
    };
  }
});
</script>

