
import { defineComponent, inject, Ref } from "vue";
import Charts from "@/components/dashboard/data-charts/industrial-video-alarm/charts.vue";
import NumAnimate from "@/components/dashboard/common/num-animate.vue";

export default defineComponent({
  name: "videoAlarm",
  components: { Charts, NumAnimate },
  props: {
    industrialVideoData: {
      type: Array,
      default: () => [0, 0, 0]
    }
  },
  setup(props) {
    const chartData1 = [props.industrialVideoData[0]];
    const chartData2 = [props.industrialVideoData[2]];
    return {
      chartData1,
      chartData2,
      colors: ["#06F398", "#06F398", "#FACB1B"],
      height: inject<Ref<number>>("height")
    };
  }
});
