<template>
  <section
    class="w100 h100 over-hidden position-relative flex-column just-start align-center"
  >
    <img
      src="../../../assets/svg/dashboard/data-charts/well-transport/transport-back.svg"
      alt="运输图"
      class="img-suite"
      :style="{ height: height * 0.8 + 'px' }"
    />
    <ul
      class="mb0 flex-column just-center lign-start position-absolute un-left-content"
    >
      <li
        class="un-number"
        :class="{
          'one-level-color': undTransport[curIndex].value >= 20,
          'two-level-color':
            undTransport[curIndex].value >= 10 &&
            undTransport[curIndex].value < 20
        }"
      >
        {{ undTransport[curIndex].value }}
      </li>
      <li class="un-text">
        {{ undTransport[curIndex].name }}
      </li>
    </ul>
    <ul
      class="mb0 flex-column just-center align-end position-absolute un-right-content"
    >
      <li
        class="un-number"
        :class="{
          'one-level-color': undTransport[curIndex + 1].value >= 20,
          'two-level-color':
            undTransport[curIndex + 1].value >= 10 &&
            undTransport[curIndex + 1].value < 20
        }"
      >
        {{ undTransport[curIndex + 1].value }}
      </li>
      <li class="un-text">
        {{ undTransport[curIndex + 1].name }}
      </li>
    </ul>
  </section>
</template>

<script lang="ts">
import {
  computed,
  defineComponent,
  inject,
  onMounted,
  PropType,
  ref,
  Ref
} from "vue";
import { useInterval } from "@/hooks/common/useTimer";

interface IView {
  curIndex: number;
  timer: number;
}

export default defineComponent({
  name: "undergroundTransport",
  props: {
    undTransport: {
      type: Array as PropType<{ name: string; value: number }[]>,
      default: () => [
        { name: "皮带急停", value: 0 },
        { name: "皮带打滑", value: 0 },
        { name: "皮带跑偏", value: 0 },
        { name: "皮带撕裂", value: 0 },
        { name: "温度异常", value: 0 },
        { name: "烟雾异常", value: 0 },
        { name: "定位异常", value: 0 },
        { name: "车辆超速", value: 0 }
      ]
    },
    wait: {
      type: Number,
      default: 3000
    }
  },
  setup(props) {
    const curIndex = ref<number>(0);
    const length = computed(() => Math.ceil(props.undTransport.length / 2));

    function setCurIndex() {
      curIndex.value = (curIndex.value + 1) % length.value;
    }

    const { openInterval } = useInterval({ cb: setCurIndex, dur: props.wait });

    onMounted(() => {
      openInterval();
    });

    return { curIndex, height: inject<Ref<number>>("height") };
  }
});
</script>

