
import { defineComponent, inject, PropType, Ref } from "vue";
import NumAnimate from "@/components/dashboard/common/num-animate.vue";

export default defineComponent({
  name: "minePressureAlarm",
  components: { NumAnimate },
  props: {
    minePressureData: {
      type: Array as PropType<{ name: string; value: number }[]>,
      default: () => [
        { name: "矿震", value: 0 },
        { name: "矿压", value: 0 },
        { name: "应力", value: 0 },
        { name: "位移", value: 0 }
      ]
    }
  },
  setup() {
    const classList = [
      "mine-earthquake",
      "mine-pressure",
      "stress",
      "displacement"
    ];
    return { classList, height: inject<Ref<number>>("height") };
  }
});
