<template>
  <div ref="chartRef" />
</template>

<script lang="ts">
import { defineComponent, onMounted, ref, Ref } from "vue";
import useEcharts from "@/hooks/common/useEchart";

export default defineComponent({
  name: "charts",
  props: {
    value: {
      type: Array,
      default: () => [0]
    },
    chartColor: {
      type: String,
      default: "#06f398"
    }
  },
  setup(props) {
    const chartRef = ref<HTMLDivElement | null>(null);
    const { setOptions } = useEcharts(chartRef as Ref<HTMLDivElement>);
    onMounted(() => {
      setOptions({
        grid: {
          bottom: "15%",
          top: 0
        },
        xAxis: {
          show: false,
          data: props.value
        },
        yAxis: {
          show: false,
          max: 100
        },
        series: [
          {
            type: "pictorialBar",
            color: "#7f71e2",
            symbol: "rect",
            symbolSize: [18, 5],
            symbolRepeat: true,
            symbolBoundingData: 100,
            data: [
              {
                value: props.value[0],
                itemStyle: {
                  color: props.chartColor
                }
              }
            ],
            z: 10
          },
          {
            type: "pictorialBar",
            itemStyle: {
              normal: {
                opacity: 0.2,
                barBorderRadius: 10
              }
            },
            symbol: "rect",
            symbolSize: [18, 5],
            symbolRepeat: "fixed",
            symbolBoundingData: 100,
            animationDuration: 30,
            data: [
              {
                value: props.value[0],
                itemStyle: {
                  color: "#616161"
                }
              }
            ],
            z: 5
          }
        ]
      });
    });
    return { chartRef };
  }
});
</script>

<style scoped lang="less"></style>
