
import {
  computed,
  defineComponent,
  inject,
  onMounted,
  PropType,
  ref,
  Ref
} from "vue";
import { useInterval } from "@/hooks/common/useTimer";

interface IView {
  curIndex: number;
  timer: number;
}

export default defineComponent({
  name: "undergroundTransport",
  props: {
    undTransport: {
      type: Array as PropType<{ name: string; value: number }[]>,
      default: () => [
        { name: "皮带急停", value: 0 },
        { name: "皮带打滑", value: 0 },
        { name: "皮带跑偏", value: 0 },
        { name: "皮带撕裂", value: 0 },
        { name: "温度异常", value: 0 },
        { name: "烟雾异常", value: 0 },
        { name: "定位异常", value: 0 },
        { name: "车辆超速", value: 0 }
      ]
    },
    wait: {
      type: Number,
      default: 3000
    }
  },
  setup(props) {
    const curIndex = ref<number>(0);
    const length = computed(() => Math.ceil(props.undTransport.length / 2));

    function setCurIndex() {
      curIndex.value = (curIndex.value + 1) % length.value;
    }

    const { openInterval } = useInterval({ cb: setCurIndex, dur: props.wait });

    onMounted(() => {
      openInterval();
    });

    return { curIndex, height: inject<Ref<number>>("height") };
  }
});
