
import {
  computed,
  defineComponent,
  inject,
  onMounted,
  PropType,
  ref,
  Ref
} from "vue";
import { useInterval } from "@/hooks/common/useTimer";

export default defineComponent({
  name: "hydrologicalTexture",
  components: {},
  props: {
    hydrologicalTextureData: {
      type: Array as PropType<{ name: string; value: number }[]>,
      default: () => [
        { name: "水位", value: 0 },
        { name: "水压", value: 0 },
        { name: "流量", value: 0 },
        { name: "流速", value: 0 },
        { name: "排水量", value: 0 },
        { name: "水质", value: 0 },
        { name: "地表", value: 0 },
        { name: "降水量", value: 0 },
        { name: "水位", value: 0 }
      ]
    },
    wait: {
      type: Number,
      default: 3000
    }
  },
  emits: {},
  setup(props) {
    const curIndex = ref<number>(0);
    const length = computed(() =>
      Math.ceil(props.hydrologicalTextureData.length / 2)
    );

    function setCurIndex() {
      curIndex.value = (curIndex.value + 1) % length.value;
    }

    const { openInterval } = useInterval({ cb: setCurIndex, dur: props.wait });

    onMounted(() => {
      openInterval();
    });
    return {
      curIndex,
      height: inject<Ref<number>>("height")
    };
  }
});
