<template>
  <div
    class="w100 h100 bk-img flex-column position-relative"
    style="padding: 2.4rem;"
  >
    <DevelopCenter :developInfo="developInfo" />
    <SiteSearch class="position-absolute site-search" />
    <section class="flex-row just-center" style="z-index: 20;">
      <DataSelect ref="searchRef" />
    </section>
    <section
      class="flex-1 flex-column just-between over-hidden position-relative"
    >
      <div class="flex-row just-between over-hidden">
        <ul class="flex-row mb0 just-between" style="width: 40%;">
          <li class="flex-column">
            <h3 class="title-flag-r">安全监测</h3>
            <DataChartsSafetyMonitor :safetyData="safetyData" :wait="wait" />
          </li>
          <li class="flex-column">
            <h3 class="title-flag-r">重大设备异常</h3>
            <MajorEquipmentAnomaly />
          </li>
        </ul>
        <ul class="mb0 flex-row just-between" style="width: 40%;">
          <li class="flex-column">
            <h3 class="title-flag-r">瓦斯报警</h3>
            <GasAlarm :gasData="gasData" />
          </li>
          <li class="flex-column">
            <h3 class="title-flag-r">工业视频报警</h3>
            <VideoAlarm :industrialVideoData="industrialVideoData" />
          </li>
        </ul>
      </div>
      <div
        class="flex-row just-between over-hidden flex-shrink"
        :style="{ height: height + 'px' }"
      >
        <ul class="mb0 flex-row just-between " style="width: 40%;">
          <li class="flex-column">
            <h3 class="title-flag-r">矿压矿震报警</h3>
            <MinePressureAlarm :minePressureData="minePressureData" />
          </li>
          <li class="flex-column">
            <h3 class="title-flag-r">水文质地异常</h3>
            <HydrologicalTexture
              :hydrologicalTextureData="hydrologicalTextureData"
              :wait="wait"
            />
          </li>
        </ul>
        <ul class="mb0 flex-row just-between" style="width: 40%;">
          <li class="flex-column">
            <h3 class="title-flag-r">井下运输监测</h3>
            <div class="flex-1 ">
              <UndergroundTransport :undTransport="undTransport" :wait="wait" />
            </div>
          </li>
          <li class="flex-column">
            <h3 class="title-flag-r">边坡监测</h3>
            <div class="flex-1 ">
              <SlopeMonitoring :sideSlopeData="sideSlopeData" :wait="wait" />
            </div>
          </li>
        </ul>
      </div>
    </section>
    <FullChange />
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, reactive, provide, Ref, ref } from "vue";
import SiteSearch from "@/components/dashboard/common/site-search.vue";
import DevelopCenter from "@/components/dashboard/data-charts/develop-center.vue";
import VideoAlarm from "@/components/dashboard/data-charts/industrial-video-alarm/videoAlarm.vue";
import MajorEquipmentAnomaly from "@/components/dashboard/data-charts/major-equipment-anomaly.vue";
import GasAlarm from "@/components/dashboard/data-charts/gasAlarm.vue";
import MinePressureAlarm from "@/components/dashboard/data-charts/minePressureAlarm.vue";
import HydrologicalTexture from "@/components/dashboard/data-charts/hydrologicalTexture.vue";
import UndergroundTransport from "@/components/dashboard/data-charts/undergroundTransport.vue";
import DataChartsSafetyMonitor from "@/components/dashboard/data-charts/data-charts-safety-monitor.vue";
import SlopeMonitoring from "@/components/dashboard/data-charts/slopeMonitoring.vue";
import { communicate } from "@/utils";
import {
  getSafetyData,
  getSideSlopeData,
  getUndTransport,
  getGasData,
  getIndustrialVideoData,
  getMinePressureData,
  getHydrologicalTextureData,
  getEquipmentAnomalyData
} from "./data-charts.simulate";
import DataSelect from "@/components/dashboard/common/data-select.vue";
import FullChange from "@/components/dashboard/common/full-change.vue";
import { dashboardStore } from "@/store/modules/dashboard";

export default defineComponent({
  name: "dataCharts",
  components: {
    FullChange,
    DataSelect,
    HydrologicalTexture,
    MinePressureAlarm,
    GasAlarm,
    VideoAlarm,
    DevelopCenter,
    SiteSearch,
    MajorEquipmentAnomaly,
    UndergroundTransport,
    SlopeMonitoring,
    DataChartsSafetyMonitor
  },
  setup() {
    const searchRef = ref<any>();
    const page = reactive({ curIndex: 0, pageLength: 3 });
    communicate.addListener((msg: string) => {
      searchRef.value.setValue(msg);
    });
    const wait = 3000;
    const developInfo = reactive({
      today: 0.5,
      has: 30,
      total: 2400,
      rest: 2369.5,
      restRatio: "76"
    });
    const height = computed(() => dashboardStore.viewPort.height * 0.3);
    provide<Ref<number>>(
      "height",
      computed(() => height.value * 0.83)
    );
    return {
      wait,
      page,
      developInfo,
      safetyData: getSafetyData(),
      sideSlopeData: getSideSlopeData(),
      undTransport: getUndTransport(),
      gasData: getGasData(),
      industrialVideoData: getIndustrialVideoData(),
      equipmentAnomalyData: getEquipmentAnomalyData(),
      minePressureData: getMinePressureData(),
      hydrologicalTextureData: getHydrologicalTextureData(),
      height,
      searchRef
    };
  }
});
</script>

